import { Link } from "react-scroll";
import { Tab } from "@ya.praktikum/react-developer-burger-ui-components";
import styles from "./Tabs.module.scss";

interface ITabs {
  current: string;
  setCurrent: (value: string) => void;
}
export const Tabs = ({ current, setCurrent }: ITabs) => {
  const scrollSection = (sectionId: string) => {
    const element = document.getElementById(sectionId);
    if (element) {
      element.scrollIntoView({ behavior: "smooth", block: "start" });
    }
  };

  return (
    <div className={`mt-5 ${styles.tabs}`}>
      <Link
        to="bun"
        smooth={true}
        offset={5}
        duration={0}
        onSetActive={() => setCurrent("")}
        containerId="containerElement"
        onClick={() => {
          setCurrent("");
          scrollSection("bun");
        }}
      >
        <Tab
          value=""
          active={current === ""}
          onClick={() => setCurrent("")}
        >
          
        </Tab>
      </Link>

      <Link
        to="sauce"
        smooth={true}
        offset={5}
        duration={0}
        onSetActive={() => setCurrent("")}
        containerId="containerElement"
        onClick={() => {
          setCurrent("");
          scrollSection("sauce");
        }}
      >
        <Tab
          value=""
          active={current === ""}
          onClick={() => setCurrent("")}
        >
          
        </Tab>
      </Link>

      <Link
        to="main"
        smooth={true}
        offset={5}
        duration={0}
        onSetActive={() => setCurrent("")}
        containerId="containerElement"
        onClick={() => {
          setCurrent("");
          scrollSection("main");
        }}
      >
        <Tab
          value=""
          active={current === ""}
          onClick={() => setCurrent("")}
        >
          
        </Tab>
      </Link>
    </div>
  );
};
